











































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import MarkdownEditor from '@/components/markdownEditor/index.vue'
import Dropdown from '@/components/header/components/dropdown.vue'
import { UserModule } from '../../store/modules/user'
import {debounce} from '../../utils/mixin'
import { detailArticle, createArticle, updateArticle } from '../../api/blog'
import {genNonDuplicateID} from '../../utils/mixin'
import uploadAvatar from '../settings/components/uploadAvatar/index.vue'
import articleType from './components/articleType.vue'

import { html_decode } from '../../utils/formateArticle'

export interface article {
  article_id: string | (string | null)[]
  title: string
  content: string
  createtime?: Date
  author?: string
  ellipsis: string
  markdown: string
  articleImg?: string
  articleType?: string
  articleTag?: string
}

@Component({
  name: 'Markdown',
  components: {
    MarkdownEditor,
    uploadAvatar,
    articleType,
    Dropdown
  }
})
export default class  extends Vue {
  private showImg: boolean = false
  private markdown:string = ''
  private title: string = ''
  private value:string = ''
  private html: string = ''
  private updateTags: string = ''
  private imgUrl: string = ''
  private articleType: string = ''
  private articleId!: string | (string | null)[]
  private height: number = document.documentElement.clientHeight - 68

  get avatar() {
    return UserModule.avatar
  }

  private getValue(event: string) {
    // 实时编写的值
  }

  @Watch('imgUrl', {immediate: true})
  private watchImgUrl(val: string) {
    this.showImg = val ? true : false
  }
  
  @Watch('height', {immediate: true})
  private watchHeight(val: number) {
    this.height = val
  }
  // 封面大图
  private handleUpload(event: string) {
    this.imgUrl = event
  }

  private async deleteImg() {
    this.imgUrl = ''
  }

  private async created() {
    this.articleId = this.$route.query.articleId
    if (this.articleId) {
      const { data } = await detailArticle({id: this.articleId})
      this.markdown = data.article.markdown
      this.title =  data.article.title
      this.imgUrl = data.article.articleImg
      this.articleType = data.article.articleType
      this.updateTags = data.article.articleTag
    }
  }
  mounted() {
    window.scrollTo(0, 0)
    window.onresize = debounce(() => {
      this.height = document.documentElement.clientHeight - 66
    }, 400,)
  }
 
  private async publish(types: {articleTag:string[]}) {
    if (this.imgUrl === '') {
      this.$message({ message: '请先上传博客封面图片', type: 'error' })
      return
    }
    this.html = (this.$refs.markdownEditor as MarkdownEditor).getHtml()
    const result = html_decode(this.markdown)
    const _html = html_decode(this.html)
    
    let newArticle: article = {
      article_id: this.articleId,
      articleType: this.articleType,
      title: this.title,
      ellipsis: _html.replace(/<[^>]+>/g, '').substring(0,100),
      content: _html,
      markdown: result, // 反编译存入数据库
      articleImg: this.imgUrl
    }
    Object.assign(newArticle, types)
    if(this.articleId) {
      await updateArticle(newArticle)
    } else {
      Object.assign(newArticle,{ article_id: genNonDuplicateID()})
      await createArticle(newArticle)
    }
    this.$router.push({path: '/'})
  } 
}
